/** @format */
@import url("https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700;900&family=Newsreader:wght@200;300;400;500;600;700;800&display=swap");

.product-container {
  width: 100%;
  min-height: 100vh;
  background-color: #f7f7f7;
  position: relative;
  contain: strict;
}
.product-contents {
  width: 90%;

  margin: 0 auto;
  color: rgb(29, 22, 22);
  padding: 6% 0 1%;
  h4 {
    font-family: "Newsreader", serif;
    color: #333333;
    font-size: 3rem;
    font-weight: 700;
    padding-left: 2.5rem;
    margin: 0;
  }
}
.product-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  .product-arrow_icons {
    width: 8rem;
    display: flex;
    gap: 0 1.7rem;
    .product-arrow_icon {
      background-color: #ececec;
      border-radius: 50%;
      width: 42px;
      height: 42px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 1.5rem;
      cursor: pointer;
      transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
      &:active {
        transform: scale(0.9);
      }
      &:hover {
        color: white;
        background-color: #181414;
      }
    }
  }
}
.product-trending {
  width: 100%;
  min-height: 26rem;
  padding-left: 3rem;
  padding-right: 0.5rem;
  padding-top: 4rem;
  display: flex;

  gap: 0 2.9rem;

  overflow-x: auto;
  overflow-y: hidden;
  transition: all 0.5s ease-in-out;
  scroll-behavior: smooth;
  &::-webkit-scrollbar {
    width: 10px;
    height: 0px;
  }

  /* Track */
  &::-webkit-scrollbar-track {
    background: #c0c0c0;
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    background: rgb(177, 177, 177);
  }

  /* Handle on hover */
  &::-webkit-scrollbar-thumb:hover {
    background: rgb(201, 201, 201);
  }
}
