/** @format */

.home-container {
  width: 100%;
  height: auto;
  position: relative;
  overflow: hidden;

  .home-bg-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;

    video {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &::before {
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    z-index: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    background: linear-gradient(
      90deg,
      rgb(0, 0, 0),
      rgba(0, 0, 0, 0.466),
      rgba(255, 255, 255, 0)
    );
  }
}
.animate-bar {
  position: fixed;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background-color: white;
  z-index: 250;

  transition: all 0.85s ease-in-out;
}

.animate-bar_active {
  left: -100%;
}
@keyframes animate-bar {
  0% {
    left: 100%;
  }
  100% {
    left: -100%;
  }
}
.home-contents {
  width: 90%;
  margin: 0 auto;
  padding: 2% 0 0;
  min-height: 100vh;
  color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  z-index: 1;
  h1 {
    font-size: 4rem;
    font-family: "Montserrat", sans-serif;
    width: 40rem;

    letter-spacing: 1.8px;

    margin-top: 1.5rem;
    font-weight: 700;

    span {
      font-weight: 400;
    }
  }
}
.explore-btn {
  border-radius: 15px;
  background-color: rgba(247, 247, 247, 0.911);
  color: rgb(7, 7, 7);
  width: 90px;
  height: 32px;
  display: grid;
  place-content: center;
  transition: all 0.7s cubic-bezier(0.19, 1, 0.22, 1);
  cursor: pointer;
  .explore-icon {
    font-size: 1.5rem;
  }
  &:hover {
    transform: translateY(-3px);
  }
  &:active {
    transform: scale(0.9);
  }
}
@media screen and (max-width: 720px) {
  .home-container {
    &::before {
      background: linear-gradient(
        90deg,
        rgb(0, 0, 0),
        rgba(0, 0, 0, 0.671),
        rgba(22, 22, 22, 0.199)
      );
    }
  }
  .home-contents {
    padding-left: 0.4rem;
    h1 {
      width: 100%;
      font-size: 3.4rem;
    }
  }
}
@media screen and (max-width: 540px) {
  .home-container {
    &::before {
      background: linear-gradient(
        90deg,
        rgb(0, 0, 0),
        rgba(0, 0, 0, 0.671),
        rgba(22, 22, 22, 0.199)
      );
    }
  }
  .home-contents {
    padding: 0;
    h1 {
      line-height: 0.95;
      margin-bottom: 2rem;
      span {
        opacity: 0.9;
        font-weight: 500;
        margin-top: 0;
        font-size: 2.8rem;
        line-height: 0.9;
      }
    }
  }
  .explore-btn {
    width: 80px;
  }
}
